"use client";
//import Link from 'next/link';

export default function ErrorBoundary({error}: {
    error: Error
}) {
  return (
    <div className='flex flex-col items-center justify-center h-screen px-4'>
      <h1 className='mt-2 text-center text-lg md:text-xl lg:text-2xl'>An error was encountered while processing your request:
      </h1>
      <h1 className='mt-2 text-center text-lg md:text-xl lg:text-2xl'>The specified profile could not be found.</h1>
      <h2>{error.message}</h2>
    </div>
  );
}
